import Modal from '../../../common/Modal';
import { Form } from 'react-bootstrap';
import { useState } from 'react';
import AddButton from '../../../common/button/AddButton';
import RowSpacer from '../../../common/styles/RowSpacer';
import { BooksitoutServer } from '../../../config/BooksitoutServer';
import toast from 'react-hot-toast';

interface Props {
    isOpen: boolean
    onHide: () => void
    year: number
}

const BookGoalAddModal: React.FC<Props> = ({ isOpen, onHide, year }) => {
    const [goalCount, setGoalCount] = useState<number>(0)
    const addGoalCount = () => {
        if (goalCount <= 0)  {
            toast.error('1     .')
            return
        }

        if (goalCount > 200) {
            toast.error(' 200   .')
            return
        }

        BooksitoutServer
            .post(`/v1/book/goals/${year}`, {goal: goalCount})
            .then(() => {
                toast.success(' .')
                onHide()
            })
            .catch(() => {
                toast.error(' .     .')
            })

            window.location.reload()
    }

    return (
        <Modal 
            isShowing={isOpen} 
            onClose={onHide} 
            titleText={` ${year}  `} 
            body={
                <Form onSubmit={(e) => {
                    e.preventDefault()
                    addGoalCount()
                }}>
                    <Form.Group>
                        <Form.Control
                            type="number"
                            placeholder="  "
                            onChange={(e) => setGoalCount(Number(e.target.value))}
                            autoFocus={true}
                        />

                        <RowSpacer size={10} />

                        <AddButton label='' onClick={addGoalCount} />
                    </Form.Group>
                </Form>
            }
        />
    )
}

export default BookGoalAddModal